<script>
  export let cartitem;
  $: totalprice = cartitem.reduce((acc, curval) => {
    return acc + curval.price;
  }, 0);
</script>

<style>
  h1 {
    color: rgb(255, 62, 0);
    text-transform: uppercase;
    font-size: 1.7em;
    font-weight: 100;
    margin: 3px;
  }
  div {
    border: 1px solid orangered;
  }
</style>

<div>
  {#each cartitem as item}
    <h1>{item.title}: ${item.price}</h1>
  {/each}
</div>
<h1>Total Price: ${totalprice}</h1>
