<script>
  import Cart from "./app/Cart.svelte";
  import Product from "./app/Product.svelte";
  import { Button } from "svelte-mui/src";

  let title = "";
  let price = "";
  let desc = "";

  let products = [];
  let cartitem = [];

  function createproduct() {
    let newprod = { title, price, desc };

    products = products.concat(newprod);
  }

  function addtocart(event) {
    let selectedtitle = event.detail;

    cartitem = cartitem.concat({
      ...products.find(prod => prod.title === title)
    });
  }
</script>

<style>
  main {
    text-align: center;
    margin: 0 auto;
  }
  input {
    color: rgb(255, 62, 0);
    text-transform: uppercase;
    font-size: 1em;
    font-weight: 300;
    width: 250px;
  }
  textarea {
    color: orangered;
    font-weight: lighter;
    font-size: 1.2em;
    text-transform: uppercase;
  }
  h1 {
    color: rgb(255, 62, 0);
    text-transform: uppercase;
    font-size: 1.7em;
    font-weight: 100;
    margin: 3px;
  }
</style>

<main>
  {#if cartitem.length === 0}
    <h1>There are no products in cart Yet!</h1>
  {:else}
    <Cart {cartitem} />
  {/if}
</main>

<main>
  <div>
    <h1>Title</h1>
    <input type="text" bind:value={title} />
  </div>
  <div>
    <h1>Price</h1>
    <input type="number" bind:value={price} />
  </div>
  <div>
    <h1>Description</h1>
    <textarea rows="5" cols="55%" type="text" bind:value={desc} />
    <br />
    <Button outlined color="dodgerblue" on:click={createproduct}>
      Create Product
    </Button>
    <br />
    <br />
  </div>

  {#if products.length === 0}
    <h1>There are no products created Yet!</h1>
  {:else}
    {#each products as prod}
      <Product
        on:addtocart={addtocart}
        title={prod.title}
        price={prod.price}
        desc={prod.desc} />
    {/each}
  {/if}
</main>
