<script>
  import { Button } from "svelte-mui/src";
  import { createEventDispatcher } from "svelte";

  export let title;
  export let price;
  export let desc;
  let dispatch = createEventDispatcher();

  function addtocart() {
    dispatch("addtocart", title);
  }
</script>

<style>
  h1 {
    color: rgb(255, 62, 0);
    text-transform: uppercase;
    font-size: 1.7em;
    font-weight: 100;
  }
  div {
    margin: 0 auto;
    box-shadow: 0 2px 8px black;
    border-radius: 5px;
    background: white;
    width: 400px;
    padding: 2px;
  }
</style>

<div>
  <h1>Title: {title}</h1>
  <h1>Price: {price}</h1>
  <h1>Description: {desc}</h1>
  <Button outlined color="dodgerblue" on:click={addtocart}>Add to cart</Button>
</div>
<br />
